/* #region page */
.page {
  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: $white-color;
  background-color: $main-text-color-black;

  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;

    img {
      transition: all $effectDuration;
      transform: scale(1);
    }

    img:hover {
      transform: scale(1.05);
      z-index: 1;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transition: all $effectDuration;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }

  &:has(.page__menu:target) {
    overflow: hidden;
  }
}

/*  #endregion */
